html,
body {
  padding: 0;
  margin: 0;
  border: 0;
}

body {
  padding: 0;
  background-color: #1b0335 !important;
}

@font-face {
  font-family: "Urbanist";
  src: url("./assets/fonts/Urbanist/Urbanist-VariableFont_wght.ttf") format("truetype");
}

@font-face {
  font-family: "Righteous";
  src: url("./assets/fonts/Righteous/Righteous-Regular.ttf") format("truetype");
}
.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.row {
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}
.col {
  flex: 1;
  position: relative;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}
.main {
  background-color: #030812;
  overflow: auto;
  background-image: url(./assets/imgs/bg.png);
  background-size: cover;
  padding-top: 300px;
  color: white;
  padding: 300px 30px 0 30px;
}

.title {
  font-family: "Urbanist";
  font-style: normal;
  font-weight: 700;
  font-size: 80px;
  line-height: 64px;
  text-align: center;
  margin-bottom: 90px;
}
.title-ages {
  background: -webkit-linear-gradient(90deg, #fdae8f, #fd1c68 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.title-description {
  font-size: 36px;
  text-align: center;
  font-family: "Urbanist";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
}
.sub-title {
  margin-top: 150px;
  text-align: center;
  font-size: 64px;
  font-family: "Urbanist";
  font-style: normal;
  font-weight: 600;
  font-size: 64px;
  line-height: 77px;
  margin-bottom: 30px;
}
.full-description {
  text-align: center;
  margin-bottom: 30px;
  font-family: "Urbanist";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 42px;
}
.text-red {
  color: #ee5074;
  font-style: italic;
}
.text-yellow {
  color: #ff7c25;
}
.noia-3 {
  width: 100%;
  height: auto;
}
.gradient-title {
  /* SKILL LIMIT */
  font-family: "Righteous";
  font-style: normal;
  font-weight: 400;
  font-size: 70px;
  line-height: 87px;
  /* identical to box height */
  text-align: center;

  background: linear-gradient(180deg, #fdae8f 0%, #ff3b99 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
  margin-bottom: 100px;
  margin-top: 100px;
}
.talent-image {
  text-align: center;
  margin-bottom: 30px;
}
.talent-limits-wrapper {
  margin-bottom: 250px;
}
.talent-limit {
  background: hsla(0, 0%, 100%, 0.2);
  border-radius: 65px;
  color: #fff;
  font-family: Inter;
  font-size: 28px;
  font-weight: 700;
  height: 115px;
  letter-spacing: -0.01em;
  margin: auto;
  order: 0;
  padding: 7px 10px;
  text-align: center;
  display: table;
}
.telent-label {
  display: table-cell;
  vertical-align: middle;
}
.send-wrapper {
  text-align: center;
}
.send-fee-wrapper {
  margin-top: -350px;
  margin-bottom: 200px;
}
.first-col,
.last-col {
  margin-top: -60px;
}
.send {
  margin: auto;
  padding: 7px 10px;

  background: rgba(255, 255, 255, 0.2);
  border-radius: 65px;

  width: 288px;
  height: 78px;

  font-family: "Work Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 130%;
  /* or 29px */
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0.01em;
  text-decoration: none;

  color: #ffffff;

  /* Inside auto layout */
  flex: none;
  order: 0;
  align-self: stretch;
  flex-grow: 1;
  border: none;
  cursor: pointer;
}
.fee {
  height: 51px;

  font-family: "Urbanist";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  /* or 122% */
  text-align: center;
  margin-top: 40px;
  /* Neutral / White */
  color: #ffffff;
}
.noia3-wrapper img {
  width: 100%;
}
.chart-wrapper img {
  width: 100%;
}
.talent-tiers img {
  width: 100%;
}

.reward {
  width: 100%;
  max-width: 360px;
  height: 360px;
  background: linear-gradient(360deg, #000000 0%, #f94063 100%), rgba(57, 57, 63, 0.17);
  border-radius: 500px;
  display: table;
}
.reward > div {
  display: table-cell;
  vertical-align: middle;
}
.reward-image {
  text-align: center;
  margin-bottom: 30px;
}
.reward-title {
  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 31px;
  line-height: 111%;
  /* or 34px */
  text-align: center;
  letter-spacing: -0.01em;
  color: #ffbebe;
}

.footer-text {
  font-size: 28px;
  line-height: 44px;
  text-align: center;
  margin-bottom: 50px;
  font-family: "Urbanist";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 44px;
}
.stake .header {
  grid-area: 3 / 1 / auto / auto;
  display: inline-flex;
  width: 100%;
  padding: 30px;
}
.stake header .logo {
  max-width: 10.5rem;
  min-height: 4.5rem;
  display: flex;
  align-items: center;
}

.stake .header .connect::before {
  content: "";
  position: absolute;
  background-size: cover;
  width: calc(100% - 0.5rem);
  height: calc(100% - 0.5rem);
  top: 0.25rem;
  right: 0.25rem;
  background-color: rgb(27, 3, 53);
  border-radius: 0.1875rem;
  z-index: -1;
  transform: scaleY(1);
  transition: all 0.3s ease-in-out 0s;
  transform-origin: center bottom;
}
.stake .header .connect::after {
  content: "";
  position: absolute;
  width: 2.125rem;
  height: 2.1875rem;
  top: -1.0625rem;
  right: 1.875rem;
  border-bottom: 0.25rem solid rgb(239, 170, 99);
  border-right: 0.25rem solid rgb(245, 191, 101);
  border-bottom-right-radius: 0.6875rem;
  background: linear-gradient(to right, rgb(197, 14, 87) 0%, rgb(254, 223, 103) 100%);
  transform: rotate(45deg);
  box-shadow: rgb(27 3 53) -0.375rem -0.375rem 0px 0.1875rem inset;
}
.stake .header .connect {
  position: relative;
  border-radius: 0.1875rem;
  background: linear-gradient(to right, rgb(197, 16, 87), rgb(252, 216, 103));
  z-index: 1;
  color: white;
  transition: all 0.3s ease-in-out 0s;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  margin-right: 0.5rem;
  text-align: center;
  width: auto;
}
.stake .stake-button {
  text-align: center;
}
.stake .stake-button button::before {
  /* content: ""; */
  position: absolute;
  background-size: cover;
  width: calc(100% - 0.5rem);
  height: calc(100% - 0.5rem);
  top: 0.25rem;
  right: 0.25rem;
  background-color: rgb(27, 3, 53);
  border-radius: 0.1875rem;
  z-index: -1;
  transform: scaleY(1);
  transition: all 0.3s ease-in-out 0s;
  transform-origin: center bottom;
  cursor: pointer;
}
.stake .stake-button button {
  position: relative;
  border-radius: 0.1875rem;
  background: linear-gradient(to right, rgb(197, 16, 87), rgb(252, 216, 103));
  z-index: 1;
  color: white;
  transition: all 0.3s ease-in-out 0s;
  padding: 1.25rem;
  text-align: center;
  width: auto;
  cursor: pointer;
  border: none;
  width: 80%;
  margin-top: 10px;

  font-family: "Sarpanch";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}
.wallet-adapter-button:not([disabled]):hover {
  background-image: linear-gradient(rgba(0, 0, 0, 0.15) 0 0);
}
.nfts-wrapper {
  overflow-wrap: wrap;
}
.nft-card {
  min-width: 250px;
  max-width: 250px;
  margin-left: 10px;
  color: white;
}
.nft-card img {
  width: 100%;
}
.nft-card .nft-image {
}
.space {
  flex: 1;
}
.stake .stake-title {
  width: 382px;
  height: 54px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 54px;
  /* identical to box height */
  display: flex;
  align-items: center;

  color: #ffffff;
  margin-top: 20px;
}
.staked-progress-title {
  margin-bottom: 20px;

  font-family: "Sarpanch";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 50px;
  text-align: center;

  color: #ffffff;
}
.staked-progress-wrapper {
  text-align: center;
}
.staked {
  background-color: #c71458;
  height: 80px;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
}
.unstaked {
  background-color: #3c3056;
  height: 80px;
  border-radius: 40px;
  width: 80%;
  margin: auto;
  overflow: hidden;
}
#loading {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -75px;
  margin-left: -75px;
  text-align: center;
  font-size: 2em;
  border-radius: 50%;
  width: 150px;
  height: 150px;
  border: 0.25rem solid rgba(136, 136, 136, 0.788);
  border-top-color: white;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes color {
  0% {
    border-color: #222;
  }
  25% {
    border-color: #f27a26;
  }
  50% {
    border-color: #ff5b00;
  }
  75% {
    border-color: #ff8b00;
  }
  100% {
    border-color: #222;
  }
}
@media (min-width: 576px) {
  .container,
  .container-sm {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container,
  .container-md,
  .container-sm {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1140px;
  }
}
